@import "video.js/dist/video-js.css";

#root {
  position: fixed;
  top: 0;
  bottom: 0;
  height: 100%;
  max-width: 100vw;
  width: 100vw;
  margin: auto;
  background-color: var(--background);
}

.ShockWallet {
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.bottom-nav-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 21px 0;
  background-color: var(--background);
  flex-shrink: 0;
}

.bottom-nav-btn {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 31px;
  text-decoration: none;
}

a.bottom-nav-btn {
  color: white;
}

a.bottom-nav-btn.active-nav-btn {
  color: var(--main-blue);
}

.page-container {
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow: auto;
  height: 100%;
}

.ptr__children {
  overflow: hidden auto !important;
}

.inputs-group {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin-bottom: 25px;
  width: 100%;
}

.unit-dropdown {
  font-size: 24px;
  border: 0;
  background-color: transparent;
  font-family: Poppins;
  font-weight: 400;
  height: 55px;
  padding-left: 26px;
  color: white;
}

.unit-dropdown option {
  background-color: var(--black);
}

.form-error {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 13px;
  border-radius: 6px;
  color: white;
  font-weight: bold;
  margin-bottom: 15px;
  background-color: var(--failure-red);
}

.inline-link {
  text-align: center;
  font-weight: 600;
  text-decoration: underline;
  margin-top: 20px;
}

@media (display-mode: standalone) {
  #root {
    height: 100% !important;
  }

  .ShockWallet {
    flex: 1;
    height: 100% !important;
  }
  
  .page-container {
    flex: 1;
  }
}